import { Button } from '@mui/material'

const BottomBorder = ({ children, click }) => {
  return (
    <Button
      variant="outlined"
      size="medium"
      onClick={click}
      sx={{ minWidth: '2.5rem', minHeight: '2.5rem', p: 0, m: 0 }}
    >
      {children}
    </Button>
  )
}

export default BottomBorder
