import {
  Button,
  Container,
  Grid,
  Input,
  Link,
  Paper,
  TextField,
  Typography,
} from '@mui/material'
import { Box } from '@mui/system'
import GitHubIcon from '@mui/icons-material/GitHub'
import FacebookIcon from '@mui/icons-material/Facebook'

const iconsStylesHeroe = {
  cursor: 'pointer',
  fontSize: '2rem',
  transition: '0.3s all ease',
  ':hover': {
    transform: 'scale(1.2)',
  },
}

const Footer = () => {
  return (
    <footer>
      <Paper>
        <Container maxWidth="md" sx={{ mt: 5, pt: 4, pb: 4 }}>
          <Grid container>
            <Grid item xs={12}>
              <Typography
                variant="h4"
                component="h4"
                color="text.primary"
                align="center"
                sx={{
                  textShadow: '-0.05em 0 red,  0.05em 0 cyan',
                  letterSpacing: '0.2rem',
                }}
              >
                Gianicola
              </Typography>
            </Grid>
            <Grid container sx={{ pt: 5, pb: 5 }}>
              <Grid
                item
                xs={12}
                md={3}
                sx={{ display: 'flex', flexDirection: 'column' }}
              >
                <Typography
                  variant="h6"
                  component="h4"
                  color="text.secondary"
                  sx={{ mb: 2 }}
                >
                  Redes Sociales
                </Typography>
                <Box sx={{ display: 'flex', gap: 1 }}>
                  <Link
                    href="https://www.facebook.com/gianicola.panichella/"
                    target="_blank"
                    noopener
                    noreferrer
                  >
                    <FacebookIcon sx={iconsStylesHeroe} color="primary" />
                  </Link>
                  <Link
                    href="https://github.com/gianicolajara"
                    target="_blank"
                    noopener
                    noreferrer
                  >
                    <GitHubIcon sx={iconsStylesHeroe} color="primary" />
                  </Link>
                </Box>
              </Grid>
              <Grid
                item
                xs={12}
                md={3}
                sx={{ display: 'flex', flexDirection: 'column' }}
              >
                <Typography
                  variant="h6"
                  component="h4"
                  color="text.secondary"
                  sx={{ mb: 2 }}
                >
                  Enlaces
                </Typography>
                <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                  <Link href="/" color="primary">
                    Home
                  </Link>
                  <Link href="/" color="primary">
                    Sobre Mi
                  </Link>
                  <Link href="/" color="primary">
                    Trabajos
                  </Link>
                </Box>
              </Grid>
              <Grid
                item
                xs={12}
                md={6}
                sx={{ display: 'flex', flexDirection: 'column' }}
              >
                <Typography
                  variant="h6"
                  component="h4"
                  color="text.secondary"
                  sx={{ mb: 2 }}
                >
                  Contacto
                </Typography>
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                  <TextField
                    type="text"
                    label="Email"
                    variant="outlined"
                    color="primary"
                  />
                  <TextField
                    type="text"
                    label="Asunto"
                    variant="outlined"
                    color="primary"
                  />
                  <TextField
                    id="outlined-multiline-flexible"
                    label="Mensaje"
                    multiline
                    rows={4}
                    color="primary"
                  />
                  <Button
                    variant="contained"
                    color="warning"
                    sx={{ width: 'min-content', alignSelf: 'flex-end' }}
                  >
                    Enviar
                  </Button>
                </Box>
              </Grid>
            </Grid>
            <Box sx={{ width: '100%' }}>
              <Typography align="center" color="main">
                Hecho por Gianicola Jara
              </Typography>
            </Box>
          </Grid>
        </Container>
      </Paper>
    </footer>
  )
}

export default Footer
