import { Container, Grid, Paper, Typography } from '@mui/material'

const Presentation = () => {
  return (
    <Container maxWidth="md" sx={{ position: 'relative', top: '-5rem' }}>
      <Paper>
        <Grid container>
          <Grid
            item
            xs={12}
            sx={{
              width: '100%',
            }}
          >
            <Paper
              sx={{
                maxHeight: 'min-content',

                p: 3,
              }}
            >
              <Typography
                variant="h5"
                component="h2"
                align="center"
                sx={{
                  fontWeight: 500,
                }}
              >
                Sobre Mi
              </Typography>
              <Typography
                variant="body1"
                align="justify"
                sx={{ pt: 2, fontWeight: 'lighter' }}
              >
                Soy un emprendedor en el rea del desarrollo web, aunque he
                tenido experiencias con el rea de multiplataformas, me gusta
                mucho la programacin, desde los 13 aos estuve interesado en
                HTML y posteriormente me interese en PHP, pase por muchas
                tecnologas como flutter y VUE para eventualmente quedarme con
                React como libreria favorita de Javascript, he trabajado para
                empresas en Colombia y Brasil como JR para el desarrollo en
                multiplataformas, ahora quiero avanzar y ser un profesional de
                React.
              </Typography>
            </Paper>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default Presentation
