import { MenuItem, Typography, useMediaQuery } from '@mui/material'
import { Box } from '@mui/system'
import { Link } from 'react-router-dom'
import DarkModeBottom from './DarkModeBottom'
import BottomBorder from './BottomBorder'
import MenuIcon from '@mui/icons-material/Menu'
import { useTheme } from '@emotion/react'
import { useState } from 'react'

const stylesItemLink = {
  position: 'relative',
  ':after': {
    width: '0%',
    height: '2px',
    content: '""',
    position: 'absolute',
    backgroundColor: 'primary.main',
    bottom: 0,
    left: 0,
    transition: '0.3s all ease',
  },
  ':hover:after': {
    width: '100%',
  },
}

const stylesUlLinks = {
  display: 'flex',
  gap: 2,
  transition: '0.3s all ease',
  '@media (max-width: 600px)': {
    position: 'absolute',
    top: 70,
    flexDirection: 'column',
    width: '100%',
    backgroundColor: 'BackgroundTransparent.main',
    gap: 0,
    padding: '1rem',
  },
}

const Nav = () => {
  const [openNav, setOpenNav] = useState(true)
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.down('sm'))

  const handleOpenNav = () => setOpenNav(!openNav)

  return (
    <nav>
      <Box
        bgcolor="BackgroundTransparent.main"
        sx={{
          width: '100%',
          height: '70px',
          position: 'fixed',
          zIndex: '100',
          display: 'flex',
          pl: 4,
          pr: 4,
          justifyContent: 'space-between',
          alignItems: 'center',
          backdropFilter: 'blur(15px)',
        }}
      >
        <Typography
          variant="h4"
          component="h4"
          sx={{
            textShadow: '-0.05em 0 red,  0.05em 0 cyan',
            letterSpacing: '0.2rem',
          }}
        >
          Gianicola
        </Typography>
        {matches && (
          <BottomBorder click={handleOpenNav}>
            <MenuIcon />
          </BottomBorder>
        )}

        <Box sx={{ ...stylesUlLinks, right: openNav ? '-150%' : '0px' }}>
          <MenuItem LinkComponent={Link} to="/" sx={stylesItemLink}>
            Home
          </MenuItem>
          <MenuItem LinkComponent={Link} to="/" sx={stylesItemLink}>
            Sobre Mi
          </MenuItem>
          <MenuItem LinkComponent={Link} to="/" sx={stylesItemLink}>
            Trabajos
          </MenuItem>
          <DarkModeBottom />
        </Box>
      </Box>
    </nav>
  )
}

export default Nav
