import Timeline from '@mui/lab/Timeline'
import TimelineItem from '@mui/lab/TimelineItem'
import TimelineSeparator from '@mui/lab/TimelineSeparator'
import TimelineConnector from '@mui/lab/TimelineConnector'
import TimelineContent from '@mui/lab/TimelineContent'
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent'
import FastfoodIcon from '@mui/icons-material/Fastfood'
import TimelineDot from '@mui/lab/TimelineDot'
import { Container, Grid, Link, Paper, Typography } from '@mui/material'
import { Box } from '@mui/system'
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount'
import PhonelinkIcon from '@mui/icons-material/Phonelink'

const trabajos = [
  {
    anio: '2018 - Presente',
    cuerpo: 'Administrador de UECLA Andes',
  },
  {
    anio: '2021 - Presente',
    cuerpo: 'Freelancer',
  },
]

const Laboral = () => {
  return (
    <Container maxWidth="md">
      <Paper sx={{ p: 5 }}>
        <Grid container>
          <Grid item xs={12}>
            <Timeline position="alternate">
              <TimelineItem>
                <TimelineOppositeContent
                  sx={{ m: 'auto 0' }}
                  align="left"
                  variant="body2"
                  color="text.secondary"
                >
                  2018 - Presente
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineConnector />
                  <TimelineDot color="secondary">
                    <SupervisorAccountIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent sx={{ py: '12px', px: 2 }}>
                  <Typography variant="h6" component="span" color="secondary">
                    Administrador Andes Virtual Moodle
                  </Typography>
                  <Typography>
                    Administrador de{' '}
                    <Link
                      href="https://andesvirtual.co.ve/"
                      target="_blank"
                      rel="noreferrer"
                      color="primary"
                    >
                      Andes Virtual
                    </Link>{' '}
                    y creacin de Apps en React y Vue para agilizar procesos
                    dentro de la propia Aula Virtual.
                  </Typography>
                </TimelineContent>
              </TimelineItem>
              <TimelineItem>
                <TimelineOppositeContent
                  sx={{ m: 'auto 0' }}
                  align="left"
                  variant="body2"
                  color="text.secondary"
                >
                  2021 - Presente
                </TimelineOppositeContent>
                <TimelineSeparator>
                  <TimelineConnector />
                  <TimelineDot color="primary">
                    <PhonelinkIcon />
                  </TimelineDot>
                  <TimelineConnector />
                </TimelineSeparator>
                <TimelineContent sx={{ py: '12px', px: 2 }}>
                  <Typography variant="h6" component="span" color="primary">
                    Freelancer Flutter y Vue
                  </Typography>
                  <Typography>
                    Creando App en Flutter y Vue para diferentes empresas
                    Colombianas
                  </Typography>
                </TimelineContent>
              </TimelineItem>
            </Timeline>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default Laboral
