import { Grid, Link, Typography } from '@mui/material'
import { Box } from '@mui/system'
import FacebookIcon from '@mui/icons-material/Facebook'
import GitHubIcon from '@mui/icons-material/GitHub'

import VideoHeroe from '../assets/videos/heroe-video.mp4'

const stylesTitleHeroe = {
  fontWeight: 500,
}

const stylesSubTitleHeroe = {
  ...stylesTitleHeroe,
  cursor: 'pointer',
  fontWeight: 'light',
  transition: '0.3s all ease',
  ':hover': {
    textShadow: '-0.04em 0 red,  0.04em 0 cyan',
    letterSpacing: '0.1rem',
  },
}

const iconsStylesHeroe = {
  cursor: 'pointer',
  fontSize: '2rem',
  transition: '0.3s all ease',
  ':hover': {
    transform: 'scale(1.2)',
  },
}

const videoStylesHeroe = {
  width: '100%',
  position: 'absolute',
  height: '100%',
  objectFit: 'cover',
}

const Heroe = () => {
  return (
    <Grid container>
      <Grid item xs={12} sx={{ height: '550px' }}>
        <Box
          sx={{
            height: '100%',
            width: '100%',
            position: 'relative',
          }}
        >
          <video loop muted autoPlay style={videoStylesHeroe}>
            <source src={VideoHeroe} type="video/mp4" />
          </video>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'row',
              width: '100%',
              height: '100%',
            }}
          >
            <Box
              sx={{
                width: '100%',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                padding: 5,
                position: 'relative',
              }}
            >
              <Typography
                variant="h1"
                component="h1"
                color="primary"
                sx={{ ...stylesTitleHeroe, lineHeight: '4.8rem' }}
              >
                Gianicola
              </Typography>
              <Typography
                variant="h4"
                component="h2"
                color="white"
                sx={stylesSubTitleHeroe}
              >
                Jara Panichella
              </Typography>
              <Typography
                variant="h6"
                component="h3"
                color="white"
                sx={stylesSubTitleHeroe}
              >
                Desarrollador Web
              </Typography>
              <Box
                sx={{
                  p: 2,
                  display: 'flex',
                  gap: 2,
                }}
              >
                <Link
                  href="https://www.facebook.com/gianicola.panichella/"
                  target="_blank"
                  noopener
                  noreferrer
                >
                  <FacebookIcon sx={iconsStylesHeroe} color="primary" />
                </Link>
                <Link
                  href="https://github.com/gianicolajara"
                  target="_blank"
                  noopener
                  noreferrer
                >
                  <GitHubIcon sx={iconsStylesHeroe} color="primary" />
                </Link>
              </Box>
            </Box>
          </Box>
        </Box>
      </Grid>
    </Grid>
  )
}

export default Heroe
