import { Container, Grid, ImageList, ImageListItem, Paper } from '@mui/material'
import { makeStyles } from '@mui/styles'
import Design1 from '../assets/images/design-1.png'
import Design2 from '../assets/images/design-2.png'
import Design3 from '../assets/images/design-3.png'
import Design4 from '../assets/images/design-4.png'
import Design5 from '../assets/images/design-5.png'
import Design6 from '../assets/images/design-6.png'
import Design7 from '../assets/images/design-7.png'

const useStyles = makeStyles({
  a: {
    display: 'block',
    height: '100%',
  },
  img: {
    objectFit: 'cover',
    width: '100%',
    height: '100%',
  },
})

const images = [
  {
    id: 1,
    image: Design1,
    row: 1,
    col: 1,
    url: 'https://landing-page-with-curved-sections.vercel.app/',
  },
  {
    id: 2,
    image: Design2,
    row: 1,
    col: 2,
    url: 'https://room-homepage-ashen.vercel.app/',
  },
  {
    id: 3,
    image: Design3,
    row: 1,
    col: 2,
    url: 'https://social-proof-section-one-phi.vercel.app/',
  },
  {
    id: 4,
    image: Design4,
    row: 2,
    col: 1,
    url: 'https://social-proof-section-one-phi.vercel.app/',
  },
  {
    id: 5,
    image: Design5,
    row: 2,
    col: 1,
    url: 'https://room-homepage-ashen.vercel.app/',
  },
  { id: 6, image: Design6, row: 1, col: 1, url: '#' },
  { id: 7, image: Design7, row: 1, col: 2, url: '#' },
]

const Designs = () => {
  const clases = useStyles()

  return (
    <Container maxWidth="md" sx={{ pt: 5, position: 'relative', top: '-50px' }}>
      <Paper sx={{ p: 5 }}>
        <Grid container>
          <Grid item xs={12}>
            <ImageList
              sx={{ width: '100%' }}
              rowHeight={400}
              variant="quilted"
              title="Diseos"
              cols={3}
              gap={8}
            >
              {images.map((item) => (
                <ImageListItem
                  cols={item.col || 1}
                  rows={item.row || 1}
                  sx={{ cursor: 'pointer' }}
                  key={item.id}
                >
                  <a
                    href={item.url}
                    target="_blank"
                    rel="noreferrer"
                    className={clases.a}
                  >
                    <img
                      src={item.image}
                      alt="diseos"
                      loading="lazy"
                      className={clases.img}
                    />
                  </a>
                </ImageListItem>
              ))}
            </ImageList>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default Designs
