import { createTheme, ThemeProvider, useMediaQuery } from '@mui/material'
import { createContext, useEffect, useState } from 'react'

const MyThemeContext = createContext()

const ThemeContextProvider = ({ children }) => {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)')
  const [darkMode, setDarkMode] = useState(prefersDarkMode)

  useEffect(() => {
    setDarkMode(prefersDarkMode)
  }, [prefersDarkMode])

  let theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      Background: {
        main: darkMode ? '#121212' : '#F9F9F9',
        dark: '#121212',
        light: '#F9F9F9',
      },
      BackgroundAlternative: {
        main: darkMode ? '#F9F9F9' : '#121212',
        dark: '#F9F9F9',
        light: '#121212',
      },
      BackgroundTransparent: {
        main: darkMode ? 'rgba(18,18,18,0.9)' : 'rgba(249,249,249,0.9)',
        dark: 'rgba(18,18,18,0.9)',
        light: 'rgba(249,249,249,0.9)',
      },
      darkModeOn: '#fff552',
      darkModeOff: '#52edff',
      main: '#31DBF0',
      primary: {
        main: '#31DBF0',
      },
    },
    components: {
      MuiCssBaseline: {
        styleOverrides: {
          body: {
            backgroundColor: darkMode ? '#121212' : '#F9F9F9',
          },
        },
      },
    },
  })

  const changeTheme = () => {
    theme = createTheme({
      palette: {
        mode: darkMode ? 'dark' : 'light',
        Background: darkMode ? '#121212' : 'white',
      },
    })
  }

  const changeDarkMode = () => {
    setDarkMode(!darkMode)
    changeTheme()
  }

  const data = {
    darkMode,
    changeDarkMode,
  }

  return (
    <MyThemeContext.Provider value={data}>
      <ThemeProvider theme={theme}>{children}</ThemeProvider>
    </MyThemeContext.Provider>
  )
}

export default MyThemeContext
export { ThemeContextProvider }
