import { Container, Grid, Paper, Typography } from '@mui/material'
import { Box } from '@mui/system'
import ImageStudies from '../assets/images/studies.jpg'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'

const Studies = () => {
  return (
    <Container maxWidth="md" sx={{ position: 'relative', top: '-50px' }}>
      <Paper sx={{ overflow: 'hidden' }}>
        <Grid
          container
          sx={{
            minHeight: '400px',
          }}
        >
          <Grid item xs={6}>
            <Box
              sx={{
                width: '100%',
                height: '100%',
                backgroundImage: `url(${ImageStudies})`,
                backgroundRepeat: 'no-repeat',
                backgroundSize: 'cover',
                backgroundPosition: 'center',
              }}
            ></Box>
          </Grid>
          <Grid item xs={6}>
            <Box
              sx={{
                p: 2,
                height: '100%',
                display: 'flex',

                justifyContent: 'center',
                flexDirection: 'column',
              }}
            >
              <Typography
                variant="h4"
                component="h2"
                align="center"
                sx={{
                  fontWeight: 500,
                }}
              >
                
              </Typography>
              <Typography
                variant="h6"
                content="h3"
                align="left"
                sx={{ pt: 1, pb: 1, fontWeight: 'bold' }}
              >
                Instituto Universitario de la Frontera (IUFRONT) -{' '}
                <Box
                  bgcolor="primary.main"
                  sx={{
                    display: 'inline-block',
                    color: 'black',
                    pl: 1,
                    pr: 1,
                    borderRadius: 2,
                  }}
                >
                  2018
                </Box>
              </Typography>
              <Typography
                variant="body1"
                align="justify"
                sx={{ fontWeight: 'lighter' }}
              >
                <ChevronRightIcon fontSize="1rem" sx={{ color: 'main' }} />{' '}
                Tecnico Superior En Informatica.
              </Typography>
              <Typography
                variant="body1"
                align="justify"
                sx={{ fontWeight: 'lighter' }}
              >
                <ChevronRightIcon fontSize="1rem" sx={{ color: 'main' }} />{' '}
                Segundo de la carrera.
              </Typography>
              <Typography
                variant="body1"
                align="justify"
                sx={{ fontWeight: 'lighter' }}
              >
                <ChevronRightIcon fontSize="1rem" sx={{ color: 'main' }} /> Mi
                proyecto de grado fue un videojuego en Unity para el
                mejoramiento del aprendizaje de lenguajes de programacin.
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default Studies
